import React from "react";
import "./Card.css";
import PropTypes from "prop-types";

const Card = ({ imagenUrl, lenguaje, fcolor, scolor }) => {
  return (
    <div
      className="card"
      style={{
        background: `linear-gradient(to left, ${fcolor}, ${scolor})`,
      }}
    >
      <img src={imagenUrl} alt="lang.svg" />
      <h3>{lenguaje}</h3>
    </div>
  );
};

Card.propTypes = {
  imagenUrl: PropTypes.string,
  lenguaje: PropTypes.string,
  fcolor: PropTypes.string,
  scolor: PropTypes.string,
};

export default Card;
