import React from "react";
import Card from "./Card";
import data from "../card.json";

const Cards = () => {
  console.log(data);

  return (
    <>
      {data.map(({url ,lang, fcolor, scolor}) => {
        return <Card imagenUrl={url} lenguaje={lang} fcolor={fcolor} scolor={scolor} />;
      })}
    </>
  );
};

export default Cards;
